/**
 * @description 
 * @param {object}  eg. {a:{b:{c:{d:111}}}}
 * @param {string} path  eg. a.b.c.d
 * @returns { * }  value
 */
const get = (object, propertyPath) => {
  let srcObj = object;
  const paths = propertyPath.split('.');
  for (let index = 0; index < paths.length; index += 1) {
    const key = paths[index];
    if (srcObj[key]) {
      srcObj = srcObj[key];
    } else {
      return undefined;
    }
  }
  return srcObj;
};

/**
 * @description 
 * @param {object} object - 
 * @return {object} 
 */
const deepClone = object => {
  const obj = object;
  let targetObj = {};
  // 
  // if (typeof obj !== 'object' || obj == null) {
  if (typeof obj !== 'object' || !obj) {
    return obj;
  }

  if (obj instanceof Array) {
    targetObj = [];
  }

  Object.keys(obj).forEach(key => {
    targetObj[key] = deepClone(obj[key]);
  });

  return targetObj;
};

const generateCURD = (path, request) => ({
  getList(data) {
    return request.get(`${path}`, { params: data });
  },
  
  detail(id) {
    return request.get(`${path}/${id}`);
  },
  
  delete(id) {
    return request.delete(`${path}/${id}`);
  },
  
  modify(data) {
    return request.post(`${path}`, data);
  }
});

export default {
  get,
  deepClone,
  generateCURD
};
