<template>
  <mavon-editor
    v-model="value" :toolbars="toolbars" @fullScreen="handleZIndex()"
    v-bind="$props"
    @change="handleChange"
    :external-link="externalLink"
    :style="'z-index:'+zIndex+';'"
  />
</template>
<script>
import { Browser } from '@framework/utils';
import { mavonEditor } from 'mavon-editor';
import { THIRDPARTY_PATH } from '@framework/config/consts';

const baseUrl = `${THIRDPARTY_PATH}/markdown`;

Browser.loadResources(`${baseUrl}/markdown/github-markdown.min.css`);
Browser.loadResources(`${baseUrl}/css/index.css`);

export default {
  name: 'gt-markdown-editor',
  components: {
    mavonEditor
  },
  data() {
    return {
      value: '',
      zIndex: 1,
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        // save: true, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true // 
      },
      baseUrl,
      externalLink: {
        markdown_css: false, // () => `${this.baseUrl}/markdown/github-markdown.min.css`,
        hljs_js: () => `${this.baseUrl}/highlightjs/highlight.min.js`,
        hljs_css: css => `${this.baseUrl}/highlightjs/styles/${css}.min.css`,
        hljs_lang: lang => `${this.baseUrl}/highlightjs/languages/${lang}.min.js`,
        katex_css: () => `${this.baseUrl}/katex/katex.min.css`,
        katex_js: () => `${this.baseUrl}/katex/katex.min.js`
      }
    };
  },
  props: {
    content: {
      type: String,
      default: ''
    },
    toolbarsFlag: {
      type: Boolean,
      default: true
    },
    subfield: {
      type: Boolean,
      default: true
    },
    defaultOpen: {
      type: String,
      default: 'preview'
    },
    editable: {
      type: Boolean,
      default: true
    }

  },

  watch: {
    content: {
      handler(val) {
        if (val !== this.html) {
          this.value = val;
        }
      },
      immediate: true
    }
  },
  methods: {
    handleChange(markdown, html) {
      this.$emit('change', markdown, html);
    },
    handleZIndex() {
      this.zIndex = this.zIndex === 1 ? 1500 : 1;
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
