/**
 * -N
 *  event[resize, scroll]
 * @param {Function} cb -
 * @param {number} delay - 100250()
 * @param {boolean} [noTrailing] - delaycallback
 *
 * @return {Function} 
 * */

export const throttle = (cb, delay, noTrailing = true) => {
  let timeoutID;
  let lastExecTime = 0;
  let cancelled = false;

  function clearTimeout() {
    if (timeoutID) clearTimeout(timeoutID);
  }

  function cancel() {
    clearTimeout();
    cancelled = true;
  }

  function wrapper(...arg) {
    const self = this;
    const elapsed = Date.now() - lastExecTime;

    if (cancelled) return;

    const exec = () => {
      lastExecTime = Date.now();
      cb.apply(self, arg);
    };

    clearTimeout();

    if (elapsed > delay) exec();

    else if (noTrailing) {
      timeoutID = setTimeout(exec, delay - elapsed);
    }
  }

  wrapper.cancel = cancel;

  return wrapper;
};

/**
 * -NN
 * ,, 
 * @param {Function} cb -
 * @param {number} delay - 100250()
 *
 * @return {Function} 
 * */
export const debounce = (cb, delay) => {
  let timeoutID;
  let cancelled = false;
  let noTrailing = false;

  function cancel() {
    if (timeoutID) clearTimeout(timeoutID);
    cancelled = true;
  }

  function wrapper(...arg) {
    const self = this;

    if (cancelled) return;

    const exec = () => {
      cb.apply(self, arg);
    };

    if (!timeoutID) exec();

    else {
      noTrailing = true;
      clearTimeout(timeoutID);
    }

    timeoutID = setTimeout(() => {
      if (noTrailing) exec();
      timeoutID = undefined;
      noTrailing = false;
    }, delay);
  }

  wrapper.cancel = cancel;

  return wrapper;
};
